import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasGrupoEmpresaConfig } from '../../../../dependencies/grupoEmpresaConfigDependency';
import { CLASS_NAME, LotePadrao } from '../../../../models/dtos/mcg/lotePadrao';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const LancamentoPadraoSearchView: FC<SearchViewProps<LotePadrao>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];

  const grupoPlanoConta = globalReducer.globalParameter.grupoPlanoConta;

  function getSearchExpression(): SearchExpression[] {
    return [
      {
        fieldName: 'grupoPlanoConta.id',
        value: grupoPlanoConta.id,
      },
    ];
  }

  return (
    <WindowComponent
      title="Manutenção de Lançamentos Padrões"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasGrupoEmpresaConfig(globalReducer.globalParameter)]}
      wikiId={2200}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'LancamentoPadraoEditView', CLASS_NAME)}
        searchExpressions={getSearchExpression}
      />
    </WindowComponent>
  );
};

export default LancamentoPadraoSearchView;
